.background-img {
  background-image: url(../assets/img/dtla4.jpg);
  background-size: cover;
  height: 100vh;
}
.content {
  height: 100vh;

  min-height: 970px;
  justify-content: center;
}

.text-container {
  margin-top: 22rem;
  .title {
    font-size: 64px;
    text-transform: uppercase;
    letter-spacing: 15px;
    line-height: 1.2;
  }
  .typed-text {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-weight: 500;
  }
}
